export default (settings) => {
  let $ = (e) => document.querySelector(e)
  let pageH = document.documentElement.clientHeight
  let pageW = document.documentElement.clientWidth
  if (pageW / pageH > settings.designW / settings.designH) {
    $("#app").style.cssText = `width: ${pageH * settings.designW / settings.designH}px;height: 100%`
    settings.scale = pageH / settings.designH
  } else {
    $("#app").style.cssText = `height: ${pageW * settings.designH / settings.designW}px;width: 100%`
    settings.scale = pageW / settings.designW
  }
  $("html").style.cssText = `font-size: ${settings.scale * 16}px`
}


