import store from '../store'

let config = {
  baseUrl: '', //后端服务基地址
  method: '',
  defaultBaseUrl: '' //缺省服务基地址
}

if (process.env.NODE_ENV == "development") {
  config.defaultBaseUrl = 'http://192.169.2.187'
  // config.defaultBaseUrl = 'http://120.77.233.70:8088'
} else if (process.env.NODE_ENV == "production") {
  config.defaultBaseUrl = 'http://192.169.2.187'
  // config.defaultBaseUrl = 'http://120.77.233.70:8088'
}

//从localStorage读取系统配置
let getBaseUrl = () => {
  let serverBaseUrl = store.state.config.baseUrl
  if (serverBaseUrl != '') {
    config.baseUrl = serverBaseUrl
  } else {
    config.baseUrl = config.defaultBaseUrl
  }
  return config.baseUrl
}
getBaseUrl()

export default {
  getBaseUrl,
  config
}



