<template>
  <div class="input-output light">
    <h3>近12月本车间投入产出比</h3>
    <div v-show="res.length" class="echarts-here" ref="echarts"></div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  data() {
    return {
      res: [],
      echarts: {
        timeLineData: []
      }
    }
  },
  computed: {
    ...mapState([
      'config'
    ])
  },
  methods: {
    getOption(option) {
      let xAxisData = option.xAxisData
      let inAmount = option.inAmount
      let outAmount = option.outAmount
      let inOutRatio = option.inOutRatio
      let lineLabel = new Image()
      lineLabel.src = require('../../../static/img/line-label.png')
      let optionLight = {
        "color": "#384757",
        "tooltip": {
          "trigger": "axis",
          "axisPointer": {
            "type": "cross",
            "crossStyle": {
              "color": "#384757"
            }
          }
        },
        "legend": [{
          "data": [
            {
              "name": "投入",
              icon: 'rect',
              "textStyle": {
                "color": "#7d838b",
                fontSize: 16 * this.config.scale
              }
            }
          ],
          "top": "0%",
          left: '10%',
          itemWidth: 20 * this.config.scale,
          "textStyle": {
            "color": "#fff",
          }
        }, {
          "data": [
            {
              "name": "产出",
              icon: 'rect',
              "textStyle": {
                "color": "#7d838b",
                fontSize: 16 * this.config.scale
              }
            }
          ],
          "top": "0%",
          left: '26%',
          itemWidth: 20 * this.config.scale,
          "textStyle": {
            "color": "#fff"
          }
        }, {
          "data": [
            {
              "name": "投入产出比",
              "icon": "path://M130.077 488.54h776.622v45.387H130.077V488.54z",
              "textStyle": {
                "color": "#7d838b",
                fontSize: 16 * this.config.scale
              }
            }
          ],
          "top": "0%",
          right: '10%',
          "textStyle": {
            "color": "#fff"
          }
        }],
        "xAxis": [
          {
            "type": "category",
            "data": xAxisData,
            "axisPointer": {
              "type": "shadow"
            },
            axisLine: {
              show: false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            },
            "axisLabel": {
              "show": true,
              interval: 0,
              "color": "#7d838b",
              "fontSize": 16 * this.config.scale
            }
          }
        ],
        "yAxis": [
          {
            "type": "value",
            "min": 0,
            "interval": 10,
            "axisLabel": {
              "show": false,
              "color": "#7d838b",
              "fontSize": 16 * this.config.scale
            },
            "axisLine": {
              "show": false,
              lineStyle: {
                color: '#7d838b'
              }
            },
            axisTick: {
              show: false
            },
            "splitLine": {
              show: false
            }
          },
          {
            "type": "value",
            "show": true,
            "axisLabel": {
              "show": false,
              "color": "#7d838b",
              "fontSize": 20 * this.config.scale
            },
            axisTick: {
              show: false
            },
            "min": 0,
            "interval": 1,
            "axisLine": {
              "show": false,
              "lineStyle": {
                "color": "#7d838b"
              }
            },
            "splitLine": {
              show: false
            }
          }
        ],
        "grid": {
          "top": "30%",
          left: "5%",
          right: "5%",
          bottom: '15%'
        },
        "series": [
          {
            "name": "投入",
            "type": "bar",
            "data": inAmount,
            "barWidth": 20 * this.config.scale,
            "itemStyle": {
              "normal": {
                "color": {
                  "type": "linear",
                  "x": 0,
                  "y": 0,
                  "x2": 0,
                  "y2": 1,
                  "colorStops": [
                    {
                      "offset": 0,
                      "color": "#0590EB"
                    },
                    {
                      "offset": 0.6,
                      "color": "#0293E6"
                    },
                    {
                      "offset": 1,
                      "color": "#000"
                    }
                  ],
                  "globalCoord": false
                }
              }
            }
          },
          {
            "name": "产出",
            "type": "bar",
            "data": outAmount,
            "barWidth": 20 * this.config.scale,
            "itemStyle": {
              "normal": {
                "color": {
                  "type": "linear",
                  "x": 0,
                  "y": 0,
                  "x2": 0,
                  "y2": 1,
                  "colorStops": [
                    {
                      "offset": 0,
                      "color": "#CC163E"
                    },
                    {
                      "offset": 0.6,
                      "color": "#5B2C40"
                    },
                    {
                      "offset": 1,
                      "color": "#000"
                    }
                  ],
                  "globalCoord": false
                }
              }
            },
            "barGap": "0"
          },
          {
            "name": "投入产出比",
            "type": "line",
            "yAxisIndex": 1,
            "data": inOutRatio,
            label: {
              show: true,
              formatter: item => (item.value * 100).toFixed(2) + '%',
              color: '#fff',
              backgroundColor: {
                image: lineLabel
              },
              fontSize: 16 * this.config.scale,
              padding: [4 * this.config.scale, 2 * this.config.scale, 6 * this.config.scale, 2 * this.config.scale]
            },
            "itemStyle": {
              "normal": {
                "color": "#ffaa00"
              }
            }
          }
        ]
      }
      return optionLight
    },
    init(option) {
      let echarts = this.$echarts.init(this.$refs.echarts)
      echarts.clear()
      echarts.setOption(option, true)
    },
    async run() {
      let options = []
      this.res = await api.GetWorkshopInOutRatio(this.workshopCode)
      this.$nextTick(() => {
        if (this.res.errcode) {
          return
        }
        let dbArray = this.$chunk(this.res, 6)
        dbArray.forEach((item, index) => {
          this.echarts.timeLineData.push(index)
          let temp = {}
          temp.xAxisData = item.map(item1 => item1.Date)
          temp.inAmount = item.map(item1 => item1.InAmount)
          temp.outAmount = item.map(item1 => item1.OutAmount)
          temp.inOutRatio = item.map(item1 => Number(item1.InOutRatio).toFixed(2))
          options.push(this.getOption(temp))
        })
        //构造数据
        let option = {
          baseOption: {
            timeline: {
              show: false,
              axisType: 'category',
              orient: 'vertical',
              autoPlay: true,
              inverse: true,
              playInterval: this.config.componentPageTurning * 1000,
              data: this.echarts.timeLineData
            }
          },
          options: options
        }
        this.init(option)
      })
    }
  }
}
</script>

<style scoped>
.input-output {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 1rem;
  overflow: hidden;
}
h3 {
  margin: 0.6rem;
  color: #fff;
}
.echarts-here {
  flex: 1;
}
</style>


