//定义修改状态操作
import * as types from './mutation-types'

const mutations = {
  [types.SAVE_CONFIG](state, payload) {
    state.config.designW = payload.designW ? payload.designW : state.config.designW
    state.config.designH = payload.designH ? payload.designH : state.config.designH
    state.config.scale = payload.scale ? payload.scale : state.config.scale
    state.config.baseUrl = payload.baseUrl ? payload.baseUrl : state.config.baseUrl
    state.config.checkboxList = payload.checkboxList ? payload.checkboxList : state.config.checkboxList
    state.config.pageRotation = payload.pageRotation ? payload.pageRotation : state.config.pageRotation
    state.config.statisticalKPI = payload.statisticalKPI ? payload.statisticalKPI : state.config.statisticalKPI
    state.config.realTimeKPI = payload.realTimeKPI ? payload.realTimeKPI : state.config.realTimeKPI
    state.config.componentPageTurning = payload.componentPageTurning ? payload.componentPageTurning : state.config.componentPageTurning
    state.config.textScrollingSpeed = payload.textScrollingSpeed ? payload.textScrollingSpeed : state.config.textScrollingSpeed
    state.config.theme = payload.theme ? payload.theme : state.config.theme
    state.config.tableContentSwitching = payload.tableContentSwitching ? payload.tableContentSwitching : state.config.tableContentSwitching
    state.config.devWait = payload.devWait ? payload.devWait : state.config.devWait
    state.config.dashboardSpeed = payload.dashboardSpeed ? payload.dashboardSpeed : state.config.dashboardSpeed
  },
  [types.SET_WORKSHOP_LIST](state, payload) {
    state.workshopList = payload
  },
}

export default mutations



