<template>
  <div :style="container">
    <div v-if="res.length" class="equip-container">
      <vue-seamless-scroll
        v-if="equipExist"
        :data="res"
        class="seamless-warp"
        :classOption="optionSingleHeightTime"
        @ScrollEnd="scrollEnd"
      >
        <div>
          <div
            style="height: calc((100vh - 6rem) / 2); display: flex; flex-direction: column; overflow: hidden;"
            v-for="i in res"
          >
            <div class="gr-row g-top">
              <div class="gr-col-3">
                <EquipmentItem v-if="i[0]" :data="i[0]"></EquipmentItem>
              </div>
              <div class="gr-col-3">
                <EquipmentItem v-if="i[1]" :data="i[1]"></EquipmentItem>
              </div>
              <div class="gr-col-3">
                <EquipmentItem v-if="i[2]" :data="i[2]"></EquipmentItem>
              </div>
              <div class="gr-col-3">
                <EquipmentItem v-if="i[3]" :data="i[3]"></EquipmentItem>
              </div>
            </div>
          </div>
        </div>
      </vue-seamless-scroll>
    </div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations, mapGetters } from 'vuex'
import EquipmentItem from '@/pages/EquipmentOperation/EquipmentItem'
import empty from '@/components/empty'

export default {
  components: {
    EquipmentItem,
    empty
  },
  computed: {
    ...mapState([
      'config',
      'workshopList'
    ]),
    ...mapGetters([
      'filtWorkshopList'
    ]),
    optionSingleHeightTime() {
      return {
        step: this.config.dashboardSpeed,
        limitMoveNum: 3
      }
    }
  },
  props: ['workshopCode'],
  data() {
    return {
      equipExist: true,
      res: '',
      container: {
        flex: 1,
        height: 0,
        display: 'flex',
        flexDirection: 'column'
      },
      swiperOption: {
        autoHeight: true,
        ovserver: true,
        autoplay: {
          delay: ''
        },
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  methods: {
    async run() {
      let temp = await api.GetWorkshopMachineRuntimeStat(this.workshopCode)
      if(temp.length > 8) {
        this.$root.$emit('startRoll')
      }
      this.res = this.$chunk(temp, 4)
    },
    runStatisticalKPI() {
      //此处暂时用不到
    },
    runRealTimeKPI() {

    },
    scrollEnd() {
      this.$root.$emit('slideNext')
    }
  },
  created() {
    this.swiperOption.autoplay.delay = this.config.componentPageTurning * 1000 //vuex的值初始化当前data
    const _this = this
    this.$root.$on('equip', (e) => {
      if (e === 0) {
        _this.equipExist = false
      }
      else {
        _this.equipExist = true
      }
    })
  },
  beforeDestroy() {
    this.$root.$off('equip')
  }
}
</script>

<style scoped>
.s-container {
  height: 100% !important;
}
.swiper-slide {
  height: 100%;
}
.equip-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.seamless-warp {
  flex: 1;
  overflow: hidden;
}
.g-top {
  height: calc((100vh - 6rem) / 2);
}
.g-bottom {
  flex: 1;
  height: 0;
}
</style>


