<template>
  <div class="workshop">
    <div class="d-l">
      <div class="d-l-t">
        <Upph ref="upph" :workshopCode="workshopCode"></Upph>
      </div>
      <div class="d-l-m">
        <InputOutput ref="inputOutput" :workshopCode="workshopCode"></InputOutput>
      </div>
      <div class="d-l-b">
        <WasteOutputRanking ref="wasteOutputRanking" :workshopCode="workshopCode"></WasteOutputRanking>
      </div>
    </div>
    <div class="d-c">
      <div class="d-c-t">
        <TodayCapacity ref="todayCapacity" :workshopCode="workshopCode"></TodayCapacity>
      </div>
      <div class="d-c-b">
        <TasksToBeProduced ref="tasksToBeProduced" :workshopCode="workshopCode"></TasksToBeProduced>
      </div>
    </div>
    <div class="d-r">
      <div class="d-r-t">
        <NowDevState ref="nowDevState" :workshopCode="workshopCode"></NowDevState>
      </div>
      <div class="d-r-m">
        <OutputRanking ref="outputRanking" :workshopCode="workshopCode"></OutputRanking>
      </div>
      <div class="d-r-b">
        <ProduceAmountTrend ref="produceAmountTrend" :workshopCode="workshopCode"></ProduceAmountTrend>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from 'vuex'
import NowDevState from '@/pages/Workshop/NowDevState'
import InputOutput from '@/pages/Workshop/InputOutput'
import TodayCapacity from '@/pages/Workshop/TodayCapacity'
import ProduceAmountTrend from '@/pages/Workshop/ProduceAmountTrend'
import WasteOutputRanking from '@/pages/Workshop/WasteOutputRanking'
import TasksToBeProduced from '@/pages/Workshop/TasksToBeProduced'
import OutputRanking from '@/pages/Workshop/OutputRanking'
import Upph from '@/pages/Workshop/Upph'

export default {
  props: ['workshopCode'],
  components: {
    NowDevState,
    InputOutput,
    TodayCapacity,
    ProduceAmountTrend,
    WasteOutputRanking,
    TasksToBeProduced,
    OutputRanking,
    Upph
  },
  computed: {
    ...mapState([
      'config',
      'workshopList'
    ]),
    ...mapGetters([
      'filtWorkshopList'
    ])
  },
  methods: {
    run() {
      // await this.$refs.upph.run() //上个生产班次KPI
      // await this.$refs.nowDevState.run()
      // await this.$refs.inputOutput.run()
      // await this.$refs.wasteOutputRanking.run()
      // await this.$refs.todayCapacity.run()
      // await this.$refs.produceAmountTrend.run()
      // await this.$refs.tasksToBeProduced.run() //待生产任务
      // await this.$refs.outputRanking.run() //近30天机长产产量排名
      let statInterval = () => {
        //上来就请求，解决setTimeout初次等待的问题
        this.runStatisticalKPI()
        //开启定时器
        this.statisticalTimer = setTimeout(async () => {
          await this.runStatisticalKPI()
          statInterval()
        }, this.config.statisticalKPI * 1000)
      }
      statInterval()
      let realInterval = () => {
        //上来就请求，解决setTimeout初次等待的问题
        this.runRealTimeKPI()
        //开启定时器
        this.realTimeTimer = setTimeout(async () => {
          await this.runRealTimeKPI()
          realInterval()
        }, this.config.realTimeKPI * 1000)
      }
      realInterval()
    },
    async runStatisticalKPI() {
      await this.$refs.upph.run() //上个生产班次KPI
      await this.$refs.inputOutput.run()
      await this.$refs.wasteOutputRanking.run()
      await this.$refs.produceAmountTrend.run()
      await this.$refs.tasksToBeProduced.run() //待生产任务
      await this.$refs.outputRanking.run() //近30天机长产产量排名
    },
    async runRealTimeKPI() {
      await this.$refs.nowDevState.run()
      await this.$refs.todayCapacity.run()
    }
  }
}
</script>

<style scoped>
.workshop {
  flex: 1;
  display: flex;
  height: 0;
}
.d-l,
.d-r {
  flex: 2;
  width: 0;
  display: flex;
  flex-direction: column;
}
.d-c {
  flex: 3;
  width: 0;
  display: flex;
  flex-direction: column;
}
.d-l-t,
.d-l-m,
.d-l-b,
.d-c-b,
.d-r-t,
.d-r-m,
.d-r-b {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 0;
}
.d-c-t {
  flex: 2;
  display: flex;
  flex-direction: column;
  height: 0;
}
</style>


