<template>
  <div class="now-devstate light">
    <h3>上个生产班次KPI</h3>
    <div v-show="res.length" class="echarts-here" ref="echarts"></div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
// import fitScreen from '@/assets/js/fitScreen'
import api from '@/api'
// import empty from '@/components/empty'
// import interval from '@/common/interval.js'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  // components: {
  //   empty
  // },
  data() {
    return {
      res: [],
      pie1: {
        fill: '',
        empty: ''
      },
      pie2: {
        fill: '',
        empty: ''
      }
    }
  },
  computed: {
    ...mapState([
      'config'
    ])
  },
  methods: {
    init() {
      let echarts = this.$echarts.init(this.$refs.echarts)
      let dataStyle = {
        normal: {
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          shadowBlur: 0,
          shadowColor: '#203665'
        }
      }
      let optionLight = {
        series: [{
          name: '第一个圆环',
          type: 'pie',
          clockWise: false,
          radius: ['60%', '70%'],
          itemStyle: dataStyle,
          hoverAnimation: false,
          center: ['25%', '50%'],
          data: [{
            value: this.pie1.fill,
            label: {
              normal: {
                rich: {
                  a: {
                    color: '#2196f3',
                    align: 'center',
                    fontSize: 20 * this.config.scale,
                    fontWeight: "bold"
                  },
                  b: {
                    color: '#fff',
                    align: 'center',
                    fontSize: 16 * this.config.scale
                  }
                },
                formatter: function (params) {
                  return "{a|" + params.value + "}" + "\n\n人均时产能";
                },
                position: 'center',
                show: true,
                textStyle: {
                  fontSize: '10',
                  fontWeight: 'normal',
                  color: '#fff'
                }
              }
            },
            itemStyle: {
              normal: {
                color: '#2196f3',
                shadowColor: '#2196f3',
                shadowBlur: 0
              }
            }
          }, {
            value: this.pie1.empty,
            name: 'invisible',
            itemStyle: {
              normal: {
                color: '#ccc'
              },
              emphasis: {
                color: '#24375c'
              }
            }
          }]
        }, {
          name: '第二个圆环',
          type: 'pie',
          clockWise: false,
          radius: ['60%', '70%'],
          itemStyle: dataStyle,
          hoverAnimation: false,
          center: ['75%', '50%'],
          data: [{
            value: this.pie2.fill,
            label: {
              normal: {
                rich: {
                  a: {
                    color: '#2196f3',
                    align: 'center',
                    fontSize: 20 * this.config.scale,
                    fontWeight: "bold"
                  },
                  b: {
                    color: '#fff',
                    align: 'center',
                    fontSize: 16 * this.config.scale
                  }
                },
                formatter: function (params) {
                  return "{a|" + params.value.toFixed(2) + "%}" + "\n\n有效工时利用率";
                },
                position: 'center',
                show: true,
                textStyle: {
                  fontSize: '10',
                  fontWeight: 'normal',
                  color: '#fff'
                }
              }
            },
            itemStyle: {
              normal: {
                color: '#2196f3',
                shadowColor: '#2196f3',
                shadowBlur: 0
              }
            }
          }, {
            value: this.pie2.empty,
            name: 'invisible',
            itemStyle: {
              normal: {
                color: '#ccc'
              },
              emphasis: {
                color: '#412a4e'
              }
            }
          }]
        },]
      }
      echarts.clear()
      echarts.setOption(optionLight, true)
    },
    async run() {
      this.res = await api.GetWorkshopKPI(this.workshopCode)
      this.$nextTick(() => {
        let resTemp = this.res[0]
        let upphRate = resTemp.PreTeamClassesUPPH.split('%')[0]
        let workHourRate = resTemp.PreTeamClassesTimeRatio.split('%')[0] * 100
        this.pie1.fill = upphRate
        this.pie1.empty = (1 - upphRate) > 0 ? (1 - upphRate) : 0
        this.pie2.fill = workHourRate
        this.pie2.empty = 100 - workHourRate
        this.init()
      })
    }
  }
}
</script>

<style scoped>
.now-devstate {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
h3 {
  margin: 0.6rem;
  color: #fff;
}
.echarts-here {
  flex: 1;
}
</style>


