<template>
  <div class="amount-trend light">
    <h3>近30天产量趋势</h3>
    <div v-show="res.length" class="echarts-here" ref="echarts"></div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  data() {
    return {
      res: [],
      echarts: {
        timeLineData: []
      }
    }
  },
  computed: {
    ...mapState([
      'config'
    ])
  },
  methods: {
    getOption(option) {
      let xAxisData = option.xAxisData
      let seriesData = option.seriesData
      let optionLight = {
        tooltip: {
        },
        grid: {
          top: '8%',
          left: '8%',
          right: '5%',
          bottom: '8%',
          containLabel: true,
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: { //坐标轴轴线相关设置。数学上的x轴
            show: true,
            lineStyle: {
              color: '#233e64'
            },
          },
          axisLabel: { //坐标轴刻度标签的相关设置
            formatter: item => item.split('-')[1] + '-' + item.split('-')[2],
            textStyle: {
              color: '#6a9cd5',
              fontSize: 16 * this.config.scale,
              margin: 15,
            },
          },
          data: xAxisData,
        }],
        yAxis: {
          type: 'value',
          min: 0,
          splitNumber: 3,
          splitLine: {
            show: false
          },
          axisLine: { //坐标轴轴线相关设置。数学上的x轴
            show: true,
            lineStyle: {
              color: '#233e64'
            },
          },
          axisLabel: {
            inside: true,
            textStyle: {
              color: '#6a9cd5',
              fontSize: 16 * this.config.scale
            },
          },
          axisTick: { show: true, },
        },
        series: [{
          name: '产量',
          type: 'line',
          smooth: true, //是否平滑曲线显示
          // 			symbol:'circle',  // 默认是空心圆（中间是白色的），改成实心圆
          symbol: 'none',

          lineStyle: {
            color: 'rgba(90,50,176, 1)',
            width: 1
          },
          areaStyle: { //区域填充样式
            normal: {
              //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
              color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(90,50,176, 0.9)' },
                { offset: 1, color: 'rgba(61,234,255, 0)' }
              ], false),
              shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
              shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
            }
          },
          data: seriesData
        }]
      }
      return optionLight
    },
    init(option) {
      this.$nextTick(() => {
        let echarts = this.$echarts.init(this.$refs.echarts)
        echarts.clear()
        echarts.setOption(option, true)
      })
    },
    async run() {
      let options = []
      this.res = await api.GetWorkshopMonthOutput(this.workshopCode)
      this.$nextTick(() => {
        if (this.res.errcode) {
          return
        }
        let dbArray = this.$chunk(this.res, 7)
        dbArray.forEach((item, index) => {
          this.echarts.timeLineData.push(index)
          let temp = {}
          temp.xAxisData = item.map(item1 => item1.Date)
          temp.seriesData = item.map(item1 => item1.OutputAmount)
          options.push(this.getOption(temp))
        })
        //构造数据

        let option = {
          baseOption: {
            timeline: {
              show: false,
              axisType: 'category',
              orient: 'vertical',
              autoPlay: true,
              inverse: true,
              playInterval: this.config.componentPageTurning * 1000,
              data: this.echarts.timeLineData
            }
          },
          options: options
        }
        this.init(option)
      })
    }
  }
}
</script>

<style scoped>
.amount-trend {
  flex: 1;
  margin: 1rem;
  display: flex;
  flex-direction: column;
}
h3 {
  margin: 0.6rem;
  color: #fff;
}
.echarts-here {
  flex: 1;
}
</style>


