<template>
  <div
    class="temp-back"
    :class="{'black-theme': config.theme === '星空黑', 'light-theme': config.theme === '天空蓝'}"
  >
    <div id="app" ref="app">
      <!-- swiper -->
      <Aside :isAsideHide="isAsideHide" :workshopList="workshopList" @setConfig="setConfig"></Aside>
      <div class="swiper">
        <swiper ref="swiper" :options="swiperOption" @slideChange="slideChange">
          <template v-for="(item, index) in filtWorkshopList">
            <swiper-slide
              v-if="item.WorkshopName.indexOf('监控中心看板') > -1"
              :key="index"
              class="swiper-slide"
            >
              <div class="swiper-item" ref="afc">
                <Header
                  @click.native="toggleAside"
                >{{item.WorkshopName.split('监控中心看板')[0]}}MES监控中心看板</Header>
                <Workshop ref="workshop" :workshopCode="item.WorkshopCode"></Workshop>
              </div>
            </swiper-slide>
            <swiper-slide v-else class="swiper-slide" :key="index">
              <div class="swiper-item" ref="afc">
                <Header
                  @click.native="toggleAside"
                >{{item.WorkshopName.split('设备运行看板')[0]}}MES设备运行看板</Header>
                <EquipmentOperation ref="workshop" :workshopCode="item.WorkshopCode"></EquipmentOperation>
              </div>
            </swiper-slide>
          </template>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations, mapGetters } from 'vuex'
import Header from '@/components/Header'
import Aside from '@/components/Aside'
import Workshop from '@/pages/Workshop'
import EquipmentOperation from '@/pages/EquipmentOperation'
import pageResize from '@/App/pageResize.js'

export default {
  name: 'App',
  components: {
    Header,
    Aside,
    Workshop,
    EquipmentOperation
  },
  data() {
    return {
      isAsideHide: false,
      statisticalTimer: '',
      realTimeTimer: '',
      swiperOption: {
        autoHeight: true,
        ovserver: true,
        loop: true,
        autoplay: {
          disableOnInteraction: false,
          delay: ''
        },
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  computed: {
    ...mapState([
      'config',
      'workshopList'
    ]),
    ...mapGetters([
      'filtWorkshopList'
    ])
  },
  methods: {
    ...mapMutations([
      'saveConfig',
      'setWorkshopList'
    ]),
    //刷新页面
    reload() {
      window.location.reload()
    },
    //切换左侧配置显示与否
    toggleAside() {
      this.isAsideHide = !this.isAsideHide
    },
    //页面整体切换的回调函数
    async slideChange() {
      this.$refs.swiper.swiper.autoplay.start()
      this.$root.$emit('equip', 0)
      await this.$nextTick(() => {
        this.$root.$emit('equip', 1)
        console.log(this.$refs.swiper.swiper.realIndex + '号轮播页加载完毕！')
      })
      this.clearTimer()
      this.startTimer()
    },
    //清除定时器
    clearTimer() {
      if (this.statisticalTimer) {
        clearTimeout(this.statisticalTimer)
      }
      if (this.realTimeTimer) {
        clearTimeout(this.realTimeTimer)
      }
    },
    //启动定时任务
    startTimer() {
      //定时请求后台数据(统计KPI)
      //判断定时器是否存在，存在则清除
      this.clearTimer()
      //启动定时任务
      this.$refs.workshop[this.$refs.swiper.swiper.realIndex].run()
    },
    //保存并刷新
    setConfig(e) {
      //关闭悬浮窗
      this.toggleAside()
      //引入这个只是为了保存页面缩放比例scale到vuex
      pageResize(this.config)
      //刷新页面
      this.reload()
    },
    wait(e) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve()
        }, e)
      })
    }
  },
  created() {
    this.swiperOption.autoplay.delay = this.config.pageRotation * 1000 //vuex的值初始化当前data
    this.$root.$on('slideNext', async () => {
      await this.wait(this.config.devWait * 1000)
      if (this.$refs.swiper.swiper.realIndex === this.config.checkboxList.length - 1) {
        this.$refs.swiper.swiper.slideTo(1)
      } else {
        this.$refs.swiper.swiper.slideNext()
      }
      this.$refs.swiper.swiper.autoplay.start()
    })
    this.$root.$on('startRoll', () => {
      this.$refs.swiper.swiper.autoplay.stop()
    })
  },
  async mounted() {
    console.clear()
    await this.$nextTick(() => {
      console.log('页面加载完毕！')
    })
    console.log(this.$refs.workshop)
    //0、设置样式
    pageResize(this.config)
    //1、首先去请求获取数据
    let workshopList = await api.GetAllWorkshop()
    //2、判断是否成功请求到数据，否则return
    if (workshopList.errcode) {
      return
    }
    //3、成功请求到数据 
    else {
      //3.1、将左侧配置栏隐藏
      this.isAsideHide = true
      await this.$nextTick(() => {
        console.log('左栏隐藏完毕！')
      })
      //3.2、将数据保存到vuex(已开启持续化)
      let finalList = []
      workshopList.forEach(item => {
        let temp = JSON.parse(JSON.stringify(item))
        temp.WorkshopName += '设备运行看板'
        item.WorkshopName += '监控中心看板'
        finalList.push(item)
        finalList.push(temp)
      })
      this.setWorkshopList(finalList)
      try {
        if (this.filtWorkshopList.length === 0) {
          this.isAsideHide = false
        }
      } catch (e) {

      } finally {
        await this.$nextTick(() => {
          console.log('轮播组件渲染完毕！')
        })
        //3.3、启动定时任务
        this.startTimer()
      }
    }
  },
  beforeDestroy() {
    this.$root.$off('slideNext')
    this.$root.$off('startRoll')
  }
}
</script>

<style scoped>
.temp-back {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.black-theme {
  background-image: url(../static/img/black-background.jpg);
}
.light-theme {
  background-image: url(../static/img/light-background.jpg);
}
#app {
  display: flex;
  flex-direction: column;
}
.swiper {
  height: 100%;
}
.swiper-item {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
</style>



