import config from './config.js'

const apiResource = {
  UserLogin: '/api/UserService/UserLogin',//用户登录

  UserChangePassWord: '/api/Sys_UserService/UserChangePassWord',//用户修改密码

  FaceTest: '/api/UserService/BaiduFaceSearch',//人脸识别

  GetUserInfo: '/api/Sys_UserService/GetUserInfo',//获取用户信息

  GetQualityCheckListPageBySqlWhere: '/api/Shop_QualityCheckService/GetQualityCheckListPageBySqlWhere',//获取机台检测信息列表

  GetMakeOrderByMachineCodeOrName: '/api/Order_WorkOrderService/GetMakeOrderByMachineCodeOrName',//获取设备施工单

  GetMachineByCodeOrName: '/api/MachineService/GetMachineByCodeOrName',//根据设备编码或名称获取设备

  GetProcessParamList: '/api/Basic_ProcessService/GetProcessParamList',//获取工序检测类型列表

  GetQualityCheckImgInfo: '/api/Shop_QualityCheckService/GetQualityCheckImgInfo',//获取机台检测相应的图片信息

  AddQualityCheck: '/api/Shop_QualityCheckService/AddQualityCheck',//添加机台检测信息

  UpdateQualityCheck: '/api/Shop_QualityCheckService/UpdateQualityCheck',//修改机台检测信息

  DeleteQualityCheck: '/api/Shop_QualityCheckService/DeleteQualityCheck',//删除机台检测信息

  GetSysNotifyIconDto: '/api/immsgservice/GetSysNotifyIconDto',//获取消息条数

  GetUnReadNotifyListByIconType: '/api/immsgservice/GetUnReadNotifyListByIconType',//获取消息通知

  GetPalletTraceBack: '/api/DashboardService/GetPalletTraceBack',//卡板追溯
//================================================看板接口（车间）=====================================
  GetAllWorkshop:'/api/DashboardService/GetAllWorkshop',//获取所有车间
  GetWorkshopWasteQtySort:'/api/DashboardService/GetWorkshopWasteQtySort',//最近30天机长废品产量排名
  GetWorkshopSubmitQtySort:'/api/DashboardService/GetWorkshopSubmitQtySort',//近30天机长产量排名
  GetWorkshopMachineStat:'/api/DashboardService/GetWorkshopMachineStat',//设备状态
  GetWorkshopMonthOutput:'/api/DashboardService/GetWorkshopMonthOutput',//最近30天产量趋势
  GetWorkshopTodayCapacity:'/api/DashboardService/GetWorkshopTodayCapacity',//单日计划/实际产能
  GetWorkshopKPI:'/api/DashboardService/GetWorkshopKPI',//车间KPI
  GetWorkshopInOutRatio:'/api/DashboardService/GetWorkshopInOutRatio',//近12月投入产出比
  GetWorkshopWaitProd:'/api/DashboardService/GetWorkshopWaitProd',//待生产任务
//================================================看板接口（设备实时）=====================================
  GetWorkshopMachineRuntimeStat:'/api/DashboardService/GetWorkshopMachineRuntimeStat',//设备运行看板
}

const setBaseUrl = (name) => {
  if (name == 'FaceTest') {
    let baseUrl = "http://192.168.202.9:8860";
    return baseUrl.concat(apiResource[name]);
  }
  return config.getBaseUrl().concat(apiResource[name]);
}

const resources = {
  UserLoginResource: () => { return setBaseUrl('UserLogin') },
  UserChangePassWordResource: () => { return setBaseUrl('UserChangePassWord') },
  FaceTestResource: () => { return setBaseUrl('FaceTest') },
  GetUserInfoResource: () => { return setBaseUrl('GetUserInfo') },
  GetQualityCheckListPageBySqlWhereResource: () => { return setBaseUrl('GetQualityCheckListPageBySqlWhere') },
  GetMakeOrderByMachineCodeOrNameResource: () => { return setBaseUrl('GetMakeOrderByMachineCodeOrName') },
  GetMachineByCodeOrNameResource: () => { return setBaseUrl('GetMachineByCodeOrName') },
  GetProcessParamListResource: () => { return setBaseUrl('GetProcessParamList') },
  GetQualityCheckImgInfoResource: () => { return setBaseUrl('GetQualityCheckImgInfo') },
  AddQualityCheckResource: () => { return setBaseUrl('AddQualityCheck') },
  UpdateQualityCheckResource: () => { return setBaseUrl('UpdateQualityCheck') },
  DeleteQualityCheckResource: () => { return setBaseUrl('DeleteQualityCheck') },
  GetSysNotifyIconDtoResource: () => { return setBaseUrl('GetSysNotifyIconDto') },
  GetUnReadNotifyListByIconTypeResource: () => { return setBaseUrl('GetUnReadNotifyListByIconType') },
  GetPalletTraceBackResource: () => { return setBaseUrl('GetPalletTraceBack') },
  //================================================看板接口（车间）=====================================
  GetAllWorkshopResource: () => { return setBaseUrl('GetAllWorkshop') },
  GetWorkshopWasteQtySortResource: () => { return setBaseUrl('GetWorkshopWasteQtySort') },
  GetWorkshopSubmitQtySortResource: () => { return setBaseUrl('GetWorkshopSubmitQtySort') },
  GetWorkshopMachineStatResource: () => { return setBaseUrl('GetWorkshopMachineStat') },
  GetWorkshopMonthOutputResource: () => { return setBaseUrl('GetWorkshopMonthOutput') },
  GetWorkshopTodayCapacityResource: () => { return setBaseUrl('GetWorkshopTodayCapacity') },
  GetWorkshopKPIResource: () => { return setBaseUrl('GetWorkshopKPI') },
  GetWorkshopInOutRatioResource: () => { return setBaseUrl('GetWorkshopInOutRatio') },
  GetWorkshopWaitProdResource: () => { return setBaseUrl('GetWorkshopWaitProd') },
  //================================================看板接口（设备实时）=====================================
  GetWorkshopMachineRuntimeStat: () => { return setBaseUrl('GetWorkshopMachineRuntimeStat') },
}

export default resources



