import $http from './http'

import apiResources from './resource'

export default {
  /**
  * @method 用户登录
  * @param {*} username 
  * @param {*} userpwd 
  */
  async UserLogin(username, userpwd, loginType = 2) {
    let params = {
      username,
      userpwd,
      loginType
    }
    return await $http.post(apiResources.UserLoginResource(), params)
  },
  /**
   * @method 用户修改密码
   * @param {*} username
   * @param {*} oldpwd
   * @param {*} newpwd
   */
  async UserChangePassWord(username, oldpwd, newpwd) {
    let params = {
      username,
      oldpwd,
      newpwd
    }
    return await $http.post(apiResources.UserChangePassWordResource(), params)
  },
  /**
   * @method 人脸识别
   * @param {*} Image base64
   */
  async FaceTest(Image) {
    let params = {
      Image
    }
    return await $http.post(apiResources.FaceTestResource(), params)
  },
  /**
    * @method 获取用户信息
    * @param {*} userId 用户Id
    */
  async GetUserInfo(userId) {
    let params = {
      userId
    }
    return await $http.post(apiResources.GetUserInfoResource(), params)
  },
  /**
   * @method 获取机台检测信息列表
   * @param {*} sqlwhere sql where语句
   * @param {*} pageInfo 分页信息
   */
  async GetQualityCheckListPageBySqlWhere(sqlwhere, pageInfo) {
    let params = {
      sqlwhere,
      pageInfo
    }
    return await $http.post(apiResources.GetQualityCheckListPageBySqlWhereResource(), params)
  },
  /**
   * @method 根据设备编码或名称获取施工单
   * @param {*} machineCodeOrName 设备编码或名称  
   */
  async GetMakeOrderByMachineCodeOrName(machineCodeOrName) {
    let params = {
      machineCodeOrName
    }
    return await $http.post(apiResources.GetMakeOrderByMachineCodeOrNameResource(), params)
  },
  /**
  * @method 根据设备编码或名称获取设备
  * @param {*} machineCodeOrName 设备编码或名称
  */
  async GetMachineByCodeOrName(machineCodeOrName) {
    let params = {
      machineCodeOrName
    }
    return await $http.post(apiResources.GetMachineByCodeOrNameResource(), params)
  },
  /**
  * @method 获取工序检测类型列表
  * @param {*} processid 工序Id  
  * @param {*} clas 检测类型 
  */
  async GetProcessParamList(processid, clas) {
    let params = {
      processid,
      clas
    }
    return await $http.post(apiResources.GetProcessParamListResource(), params)
  },
  /**
  * @method 获取机台检测相应的图片信息
  * @param {*} Id 主键Id  
  */
  async GetQualityCheckImgInfo(Id) {
    let params = {
      Id
    }
    return await $http.post(apiResources.GetQualityCheckImgInfoResource(), params)
  },
  /**
  * @method 添加机台检测信息
  * @param {*} check 机台首样检实体
  * @param {*} userInfo 用户信息 
  * @param {*} fileObj 图片信息 
  */
  async AddQualityCheck(check, userInfo, fileObj) {
    let params = {
      check,
      userInfo
    }
    if (fileObj) {
      params.fileObj = fileObj;
    }
    return await $http.post(apiResources.AddQualityCheckResource(), params)
  },
  /**
  * @method 修改机台检测信息
  * @param {*} check 机台首样检实体
  * @param {*} userInfo 用户信息
  * @param {*} fileObj 图片信息 
  */
  async UpdateQualityCheck(check, userInfo, fileObj) {
    let params = {
      check,
      userInfo
    }
    if (fileObj) {
      params.fileObj = fileObj;
    }
    return await $http.post(apiResources.UpdateQualityCheckResource(), params)
  },
  /**
  * @method 删除机台检测信息
  * @param {*} check 机台首样检实体
  * @param {*} userInfo 用户信息
  * @param {*} fileObj 图片信息 
  */
  async DeleteQualityCheck(check, userInfo, isHasImg) {
    let params = {
      check,
      userInfo
    }
    if (isHasImg) {
      params.isHasImg = isHasImg;
    }
    return await $http.post(apiResources.DeleteQualityCheckResource(), params)
  },
  /**
    * @method 获取通知页面的条数
    * @param {*} userId 用户编码
    */
  async GetSysNotifyIconDto(userId) {
    let params = {
      userId
    }
    return await $http.post(apiResources.GetSysNotifyIconDtoResource(), params)
  },
  /**
  * @method 获取通知页面信息
  * @param {*} userId 用户编码
  * @param {*} iconType 用户编码
  */
  async GetUnReadNotifyListByIconType(userId, iconType) {
    let params = {
      userId,
      iconType
    }
    return await $http.post(apiResources.GetUnReadNotifyListByIconTypeResource(), params)
  },
  /**
   * @method 卡板追溯
   * @param {*} workOrderCode 
   * @param {*} palletCode 
   */
  async GetPalletTraceBack(workOrderCode, palletCode) {
    let params = {
      workOrderCode,
      palletCode
    }
    return await $http.post(apiResources.GetPalletTraceBackResource(), params)
  },
  /**
   * @method 获取所有车间
   */
  async GetAllWorkshop() {
    let params = {
    }
    return await $http.post(apiResources.GetAllWorkshopResource(), params)
  },
  /**
   * @method 最近7天机长废品产量排名
   * @param {*} workshopCode 
   */
  async GetWorkshopWasteQtySort(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopWasteQtySortResource(), params)
  },
  /**
   * @method 当月机长产量排名
   * @param {*} workshopCode 
   */
  async GetWorkshopSubmitQtySort(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopSubmitQtySortResource(), params)
  },
  /**
   * @method 设备状态
   * @param {*} workshopCode 
   */
  async GetWorkshopMachineStat(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopMachineStatResource(), params)
  },
   /**
   * @method 近30天产量趋势
   * @param {*} workshopCode 
   */
  async GetWorkshopMonthOutput(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopMonthOutputResource(), params)
  },
  /**
   * @method 单日计划/实际产能
   * @param {*} workshopCode 
   */
  async GetWorkshopTodayCapacity(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopTodayCapacityResource(), params)
  },
  
  /**
   * @method 车间KPI
   * @param {*} workshopCode 
   */
  async GetWorkshopKPI(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopKPIResource(), params)
  },
   /**
   * @method 近12月投入产出比
   * @param {*} workshopCode 
   */
  async GetWorkshopInOutRatio(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopInOutRatioResource(), params)
  },
  /**
   * @method 待生产任务
   * @param {*} workshopCode 
   */
  async GetWorkshopWaitProd(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopWaitProdResource(), params)
  },
    /**
   * @method 设备实时
   * @param {*} workshopCode 
   */
  async GetWorkshopMachineRuntimeStat(workshopCode) {
    let params = {
      workshopCode
    }
    return await $http.post(apiResources.GetWorkshopMachineRuntimeStat(), params)
  },
}



