<template>
  <div class="now-devstate light">
    <h3>当前设备状态</h3>
    <div v-show="res.length" class="echarts-here" ref="echarts"></div>
    <empty v-show="!res.length"></empty>
    <!-- <empty v-else/> -->
  </div>
</template>

<script>
// import fitScreen from '@/assets/js/fitScreen'
import api from '@/api'
// import empty from '@/components/empty'
// import interval from '@/common/interval.js'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  // components: {
  //   empty
  // },
  data() {
    return {
      res: [],
      echarts: {
        legendData: '',
        seriesData: ''
      }
    }
  },
  computed: {
    ...mapState([
      'config'
    ])
  },
  methods: {
    init() {
      let echarts = this.$echarts.init(this.$refs.echarts)
      let optionLight = {
        color: ['#00FFFF', '#58595B', '#0590EC', '#F0A943', '#CE2357', '#014853'],
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        grid: {
          left: '10%',
          right: '35%',
          top: '26%',
          bottom: '16%',
          containLabel: true
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          left: '70%',
          top: '3%',
          itemWidth: 18 * this.config.scale,
          itemHeight: 12 * this.config.scale,
          itemGap: 6 * this.config.scale,
          data: ['待机', '空车', '生产', '调试', '报警', '断电'],
          textStyle: {
            color: '#fff',
            fontSize: 18 * this.config.scale
          }
        },
        series: [
          {
            type: 'pie',
            name: '运行数',
            center: ['35%', '59%'],
            radius: ['56%', '70%'],
            label: {
              normal: {
                color: '#fff',
                formatter: '{c}台',
                fontSize: 20 * this.config.scale
              }
            },
            data: this.echarts.seriesData
          }
        ]
      }
      echarts.clear()
      echarts.setOption(optionLight, true)
    },
    async run() {
      this.res = await api.GetWorkshopMachineStat(this.workshopCode)
      this.$nextTick(() => {
        this.echarts.legendData = this.res.map(item => item.StateName)
        // this.echarts.seriesData = this.res.map(item => ({
        //   name: item.StateName,
        //   value: item.MachineAmount
        // }))
        let allState = ['待机', '空车', '生产', '调试', '报警', '断电']
        this.echarts.seriesData = allState.map(item => {
          let index = this.echarts.legendData.indexOf(item)
          if (index > -1) {
            return {
              name: item,
              value: this.res[index].MachineAmount
            }
          } else {
            return {
              name: item,
              value: 0
            }
          }
        })

        this.init()
      })
    }
  }
}
</script>

<style scoped>
.now-devstate {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
h3 {
  margin: 0.6rem;
  color: #fff;
}
.echarts-here {
  flex: 1;
}
</style>


