//计算过滤操作，方便组件访问
export default {
    filtWorkshopList: state => {
        if(state.workshopList && state.config.checkboxList ) {  
            let realList = []
            state.workshopList.forEach(item => {
                if(state.config.checkboxList.indexOf(item.WorkshopName) > -1) {
                    realList.push(item)
                }
            })
            return realList
        }
    }
}


