<template>
  <div class="today-capacity">
    <div v-show="res.length" class="echarts-here" ref="echarts"></div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  data() {
    return {
      res: [],
      actualCapacityList: [],
      ratedCapacityList: [],
      max: '',
      echarts: {
        timeLineData: []
      }
    }
  },
  computed: {
    ...mapState([
      'config'
    ])
  },
  methods: {
    getOption(e, max) {
      let indicator = e.indicator
      let isLight = e.isLight
      let dataPlan = e.dataPlan
      let dataActual = e.dataActual
      let option = {
        tooltip: {},
        legend: {
          left: '6%',
          top: '5%',
          orient: 'vertical',
          textStyle: {
            fontSize: 20 * this.config.scale,
            color: '#FFF'
          },
          data: ['当日额定产能', '当日实际产能']
        },
        radar: {
          name: {
            fontSize: 15 * this.config.scale
          },
          center: ['50%', '54%'],
          radius: '65%',
          startAngle: 90,
          splitNumber: 6,
          shape: 'circle',
          splitArea: {
            areaStyle: {
              color: ['transparent']
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: '#5E94B8'
            }
          },
          indicator: indicator.map(item => ({
            max: max * 1.2 == 0 ? 1 : max * 1.2,
            name: item,
            color: '#FFF'
          }))
        },
        series: [{
          name: '当日额定产能',
          type: 'radar',
          symbol: isLight ? 'circle' : 'none',
          symbolSize: 20 * this.config.scale,
          itemStyle: {
            normal: {
              color: {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [{
                  offset: 0, color: 'white'
                }, {
                  offset: 1, color: 'red'
                }],
                global: false
              }
            }
          },
          areaStyle: {
            normal: {
              color: isLight ? 'rgba(180,88,93,.2)' : {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [{
                  offset: 0, color: 'transparent' // 0% 处的颜色
                }, {
                  offset: 1, color: '#00E3FA' // 100% 处的颜色
                }],
                global: false // 缺省为 false
              }
            }
          },
          lineStyle: {
            normal: {
              type: 'dashed',
              color: '#B4585D',
              width: 2
            }
          },
          data: [{
            value: dataPlan,
            label: {
              normal: {
                show: true,
                color: '#FFF',
                fontSize: 20 * this.config.scale,
                formatter: (params) => params.value
              }
            }
          }]
        }, {
          name: '当日实际产能',
          type: 'radar',
          symbol: isLight ? 'circle' : 'none',
          symbolSize: 20 * this.config.scale,
          itemStyle: {
            normal: {
              color:
              {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [{
                  offset: 0, color: 'white'
                }, {
                  offset: 1, color: 'blue'
                }],
                global: false
              }
            }
          },
          areaStyle: {
            normal: {
              color: isLight ? 'rgba(180,88,93,.2)' : {
                type: 'radial',
                x: 0.5,
                y: 0.5,
                r: 0.5,
                colorStops: [{
                  offset: 0, color: 'transparent' // 0% 处的颜色
                }, {
                  offset: 1, color: isLight ? '#00E3FA' : '#DA8415'  // 100% 处的颜色
                }],
                global: false // 缺省为 false
              }
            }
          },
          lineStyle: {
            normal: {
              color: '#0084CA',
              width: 2,
              type: 'dashed'
            }
          },
          data: [{
            value: dataActual,
            label: {
              normal: {
                show: true,
                color: '#fff',
                fontSize: 20 * this.config.scale,
                formatter: (params) => params.value
              }
            }
          }
          ]
        }]
      }
      return option
    },
    init(option) {
      // let op1 = {}
      // op1.indicator = ['烫金1', '烫金2', '烫金3', '烫金4', '烫金5', '烫金6', '烫金7', '烫金8', '烫金9', '烫金10', '烫金11']
      // op1.isLight = true
      // op1.dataPlan = [60, 61, 62, 61, 60, 59, 57, 60, 61, 62, 63]
      // op1.dataActual = [61, 62, 65, 69, 70, 71, 80, 73, 78, 70, 77]
      let echarts = this.$echarts.init(this.$refs.echarts)
      // let option = this.getOption(op1)
      echarts.clear()
      echarts.setOption(option, true)
    },
    async run() {
      let options = []
      this.res = await api.GetWorkshopTodayCapacity(this.workshopCode)
      this.$nextTick(() => {
        if (this.res.errcode) {
          return
        }
        this.actualCapacityList = this.res.map(item => Number(item.ActualCapacity))
        this.ratedCapacityList = this.res.map(item => Number(item.RatedCapacity))
        this.actualCapacityList = Math.max(...this.actualCapacityList)
        this.ratedCapacityList = Math.max(...this.ratedCapacityList)
        this.max = Math.max(this.actualCapacityList, this.ratedCapacityList)
        let dbArray = this.$chunk(this.res, 6)
        dbArray.forEach((item, index) => {
          this.echarts.timeLineData.push(index)
          let temp = {}
          temp.indicator = item.map(item1 => item1.MachineName)
          temp.isLight = true
          temp.dataPlan = item.map(item1 => item1.RatedCapacity)
          let dataPlanMax = Math.max(...temp.dataPlan)
          temp.dataActual = item.map(item1 => item1.ActualCapacity)
          let dataActualMax = Math.max(...temp.dataActual)
          let max = Math.max(dataPlanMax, dataActualMax)
          options.push(this.getOption(temp, max))
        })
        //构造数据

        let option = {
          baseOption: {
            timeline: {
              show: false,
              axisType: 'category',
              orient: 'vertical',
              autoPlay: true,
              inverse: true,
              playInterval: this.config.componentPageTurning * 1000,
              data: this.echarts.timeLineData
            }
          },
          options: options
        }
        this.init(option)
      })
    }
  }
}
</script>

<style scoped>
.today-capacity {
  margin: 2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
h3 {
  margin: 0.6rem;
  color: #fff;
}
.echarts-here {
  flex: 1;
}
</style>


