<template>
  <div class="waste light">
    <h3>近30天机长废品产量排名</h3>
    <div v-show="res.length" class="table">
      <vue-seamless-scroll :class-option="rollOption" :data="res">
        <div v-for="(item, index) in res" :key="index" class="tr" :class="{gradient: index%2 == 0}">
          <div class="td-no">NO.{{item.Id}}</div>
          <div class="td-person">
            <img src="../../../static/img/person.png">
          </div>
          <div class="td-name">{{item.CaptainName}}</div>
          <div class="td-sum">产量总量</div>
          <div class="td-amount">{{item.WasteQty}}</div>
        </div>
      </vue-seamless-scroll>
    </div>
    <!-- <swiper v-show="res.length" ref="swiper" :options="swiperOption" @slideChange="slideChange">
      <swiper-slide v-for="(item, index) in filtWorkshopList" :key="index" class="swiper-slide">
        <div class="swiper-item">
          <Header @click.native="toggleAside">{{item.WorkshopName}}看板监控中心</Header>
          <Workshop v-if="isMainSeen" ref="workshop" :workshopCode="item.WorkshopCode"></Workshop>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper> -->
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  data() {
    return {
      res: [],
      resChunk: [],
      isPageChange: false,
      rollOption: {
        step: ''
      },
      autoplaySpeed: 2000
    }
  },
  computed: {
    ...mapState([
      'config'
    ]),
  },
  methods: {
    async run() {
      this.res = await api.GetWorkshopWasteQtySort(this.workshopCode)
      if (this.res.errcode) {
        return
      }
    }
  },
  created() {
    this.rollOption.step = this.config.textScrollingSpeed
  }
}
</script>

<style scoped>
.gradient {
  background: linear-gradient(to right, transparent, #691234);
}
.waste {
  flex: 1;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  color: #fff;
}
h3 {
  margin: 0.6rem;
}
.table {
  flex: 1;
  height: 0;
  overflow: hidden;
  font-size: 1rem;
  padding: 0.5rem;
}
.tr {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}
.td-no {
  flex: 1;
  overflow: hidden;
}
.td-person {
  flex: 1;
  overflow: hidden;
}
.td-name {
  flex: 1;
  overflow: hidden;
}
.td-sum {
  flex: 1;
  overflow: hidden;
}
.td-amount {
  flex: 1;
  text-align: right;
  overflow: hidden;
  padding-right: 1rem;
}
</style>


