<template>
  <div class="waste light">
    <h3>待生产任务</h3>
    <div class="title">
      <div class="title-no">机台</div>
      <div class="title-person">产品名称</div>
      <div class="title-name">计划数量</div>
      <div class="title-sum">生产进度</div>
      <div class="title-amount">交货日期</div>
    </div>
    <div v-show="res.length" class="table">
      <vue-seamless-scroll style="margin-top: 30px;" :class-option="rollOption" :data="res">
        <div v-for="(item, index) in res" :key="index" class="tr" :class="{gradient: index%2 == 0}">
          <div class="td-no">{{item.MachineAndProcess}}</div>
          <div class="td-person">{{item.ProductName}}</div>
          <div class="td-name">{{Number(item.PlanAmount).toFixed(2)}}</div>
          <div class="td-sum">{{Number(item.ratio.split('%')[0]).toFixed(2) + '%'}}</div>
          <div class="td-amount">{{item.DeliveryDate.split(' ')[0]}}</div>
        </div>
      </vue-seamless-scroll>
    </div>
    <empty v-show="!res.length"></empty>
  </div>
</template>

<script>
import api from '@/api'
import { mapState, mapMutations } from 'vuex'
import empty from '@/components/empty'

export default {
  components: {
    empty
  },
  props: ['workshopCode'],
  data() {
    return {
      res: [],
      resChunk: [],
      isPageChange: false,
      rollOption: {
        step: ''
      },
      autoplaySpeed: 2000
    }
  },
  computed: {
    ...mapState([
      'config'
    ]),
  },
  methods: {
    async run() {
      this.res = await api.GetWorkshopWaitProd(this.workshopCode)
      if (this.res.errcode) {
        return
      }
    }
  },
  created() {
    this.rollOption.step = this.config.textScrollingSpeed
  }
}
</script>

<style scoped>
.gradient {
  background: linear-gradient(to bottom, transparent, #1d2c4d);
}
.waste {
  flex: 1;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  color: #fff;
}
h3 {
  margin: 0.6rem;
}
.table {
  flex: 1;
  height: 0;
  overflow: hidden;
  font-size: 1rem;
  padding: 0.5rem;
}
.tr {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  padding-top: 3px;
  padding-bottom: 3px;
}
.tr > div {
  white-space: nowrap;
  text-overflow: ellipsis;
}
.td-no {
  flex: 1;
  overflow: hidden;
}
.td-person {
  flex: 1;
  overflow: hidden;
}
.td-name {
  flex: 1;
  overflow: hidden;
  text-align: right;
}
.td-sum {
  flex: 1;
  overflow: hidden;
  text-align: right;
}
.td-amount {
  flex: 1;
  text-align: right;
  overflow: hidden;
  padding-right: 1rem;
}
.title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  padding-top: 3px;
  padding-bottom: 3px;
}
.title-no {
  flex: 1;
  overflow: hidden;
}
.title-person {
  flex: 1;
  overflow: hidden;
}
.title-name {
  flex: 1;
  overflow: hidden;
  text-align: right;
}
.title-sum {
  flex: 1;
  overflow: hidden;
  text-align: right;
}
.title-amount {
  flex: 1;
  overflow: hidden;
  padding-right: 2rem;
  text-align: right;
}
</style>


