<template>
  <div class="light equip-container">
    <div class="g-top">
      <div class="title-1">
        <div class="u-circle" :class="{
          'state-1': data.stat === '断电',
          'state-2': data.stat === '待机',
          'state-3': data.stat === '报警',
          'state-4': data.stat === '生产',
          'state-5': data.stat === '运行',
          'state-6': data.stat === '调试',
          'state-7': data.stat === '空车'
        }"></div>{{data.machinecode}}
      </div>
      <div class="title-2">{{data.machinename}}</div>
    </div>
    <div class="g-center">
      <div class="index-row-1">
        <div class="index-left animated bounceInDown">{{data.stat}}</div>
        <div class="index-center">{{data.completeradio}}</div>
        <div class="index-right">{{data.speed}}</div>
      </div>
      <div class="index-row-2">
        <div class="index-left">设备状态</div>
        <div class="index-center">完成率</div>
        <div class="index-right">速度（{{data.SpeedUnit}}）</div>
      </div>
    </div>
    <div class="g-bottom">
      <div class="info">
        <div class="info-left">当前产品</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.Basic_ProductName : ''}}</div>
      </div>
      <div class="info">
        <div class="info-left">当前工单</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.Order_WorkOrderCode : ''}}</div>
      </div>
      <div class="info">
        <div class="info-left">当前工序</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.Basic_PrecessName : ''}}</div>
      </div>
      <div class="info">
        <div class="info-left">计划产量</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.PlanAmount : ''}}{{data.Order_WorkOrderCode ? data.Basic_UnitOutCode : ''}}</div>
      </div>
      <div class="info">
        <div class="info-left">完成产量</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.SubmitQty : ''}}</div>
      </div>
      <div class="info">
        <div class="info-left">当前班组</div>
        <div class="info-right">{{data.Order_WorkOrderCode ? data.teamname : ''}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['data']
}
</script>

<style scoped>
* {
  font-family: 黑体;
}
.state-1 {
  background-color: #e6e6e6;
}
.state-2 {
  background-color: #fff;
}
.state-3 {
  background-color: #f8e71d;
}
.state-4 {
  background-color: #00c856;
}
.state-5 {
  background-color: #48D2B0;
}
.state-6 {
  background-color: #ff7733;
}
.state-7 {
  background-color: #99A3E7;
}
.equip-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 1rem;
  background: url(../../../static/img/equip-back.png);
  background-size: 100% 100%;
  color: #fff;
}
.g-top {
  flex: 62;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}
.g-center {
  flex: 92;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.index-row-1 {
  display: flex;
  font-size: 2rem;
}
.index-row-2 {
  display: flex;
  font-size: 0.8rem;
  position: relative;
  top: 0.2rem;
  color: #b6cff2;
}
.index-left {
  flex: 133;
  width: 0;
  display: flex;
  justify-content: center;
}
.index-center {
  flex: 150;
  width: 0;
  display: flex;
  justify-content: center;
}
.index-right {
  flex: 130;
  width: 0;
  display: flex;
  justify-content: center;
}
.g-bottom {
  flex: 232;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-left: 2rem;
}
.info {
  font-size: 1.1rem;
  font-weight: lighter;
  display: flex;
}
.info .info-right {
  position: relative;
  left: 2.4rem;
}
.u-circle {
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 100%;
  margin-right: 1rem;
}
.title-1,
.title-2 {
  font-size: 1.2rem;
}
.title-1 {
  display: flex;
  align-items: center;
}
</style>


