<!--此页面完全可以独立完成全局配置的修改-->
<template>
  <div class="aside" :class="{'aside-hide': isAsideHide}">
    <div class="input-group">
      <h4>设计图</h4>
      <label for="designW">宽(像素):</label>
      <input id="designW" v-model="designW" type="number" min="1" />
      <label for="designH">高(像素):</label>
      <input id="designH" v-model="designH" type="number" min="1" />
    </div>
    <div class="input-group other">
      <h4>系统参数</h4>
      <label for="getBaseUrl">服务器地址:</label>
      <input id="getBaseUrl" style="width: 15rem;" type="text" v-model="getBaseUrl" />
      <br />
      <label for="getPageInterval">页面轮播循环时间(秒):</label>
      <input id="getPageInterval" type="number" v-model="pageRotation" />
      <br />
      <label for="getStatisKPI">统计KPI更新时间(秒):</label>
      <input id="getStatisKPI" type="number" v-model="statisticalKPI" />
      <br />
      <label for="getRealKPI">实时KPI更新时间(秒):</label>
      <input id="getRealKPI" type="number" v-model="realTimeKPI" />
      <br />
      <label for="getComInterval">组件翻页时间(秒):</label>
      <input id="getComInterval" type="number" v-model="componentPageTurning" />
      <br />
      <label for="getTextSpeed">文字滚动速度:</label>
      <input id="getTextSpeed" type="number" v-model="textScrollingSpeed" />
      <br />
      <label for="getDashboardSpeed">设备运行看板滚动速度:</label>
      <input id="getDashboardSpeed" type="number" v-model="dashboardSpeed" />
      <br />
      <label for="getDevWait">设备运行看板等待时间(秒):</label>
      <input id="getDevWait" type="number" v-model="devWait" />
      <br />
    </div>
    <div class="input-group other">
      <h4>功能配置</h4>
      <input name="多选项名称" type="checkbox" v-model="isCheckAll" id="all" @click="cli" /> 全选
      <br />
      <span>车间看板:</span>
      <br />
      <div class="con">
        <span
          style="display:inline-block; width: 42%;"
          v-for="(item, index) in workshopList"
          :key="index"
        >
          <input
            name="cc"
            type="checkbox"
            :value="item.WorkshopName"
            v-model="checkboxList"
          />
          {{item.WorkshopName}}
        </span><br>
      </div>
    </div>
    <div class="input-group other">
      <h4>其他</h4>
      <span>主题切换:</span>
      <input id="theme-black" checked name="theme" type="radio" v-model="theme" value="星空黑" />
      <label for="theme-black">星空黑</label>
      <input id="theme-blue" name="theme" type="radio" v-model="theme" value="天空蓝" />
      <label for="theme-blue">天空蓝</label>
      <br />
      <!-- <span>表格内容切换:</span>
      <input
        id="table-row"
        checked
        name="table"
        type="radio"
        v-model="tableContentSwitching"
        value="逐条"
      >
      <label for="table-row">逐条</label>
      <input id="table-page" name="table" type="radio" v-model="tableContentSwitching" value="翻页">
      <label for="table-page">翻页</label>-->
    </div>
    <div class="input-group">
      <button @click="setConfig">保存并刷新</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  props: {
    isAsideHide: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState([
      'config',
      'workshopList'
    ])
  },
  data() {
    return {
      designW: '',
      designH: '',
      checkboxList: '',
      isCheckAll: true, //以下为配置处数据
      getBaseUrl: '',
      pageRotation: '', //页面轮播循环时间(秒)
      statisticalKPI: '', //统计KPI更新时间(秒)
      realTimeKPI: '', //实时KPI更新时间(秒)
      componentPageTurning: '',//组件翻页时间(秒)
      textScrollingSpeed: '',//文字滚动速度
      dashboardSpeed: '',//设备运行看板滚动速度
      devWait: '',
      theme: '星空黑',//主题切换
      tableContentSwitching: '逐条',//表格内容切换
    }
  },
  methods: {
    ...mapMutations([
      'saveConfig',
      'setWorkshopList'
    ]),
    initData() {
      this.designW = this.config.designW || 1920
      this.designH = this.config.designH || 1080
      this.pageRotation = this.config.pageRotation //页面轮播循环时间(秒)
      this.statisticalKPI = this.config.statisticalKPI//统计KPI更新时间(秒)
      this.realTimeKPI = this.config.realTimeKPI //实时KPI更新时间(秒)
      this.componentPageTurning = this.config.componentPageTurning//组件翻页时间(秒)
      this.textScrollingSpeed = this.config.textScrollingSpeed//文字滚动速度
      this.textScrollingSpeed = this.config.textScrollingSpeed//文字滚动速度
      this.theme = this.config.theme//主题切换
      this.devWait = this.config.devWait//表格内容切换
      this.dashboardSpeed = this.config.dashboardSpeed//表格内容切换
      this.checkboxList = this.config.checkboxList ? this.config.checkboxList : this.workshopList.map(item => item.WorkshopName)
      let settings = {
        checkboxList: this.checkboxList
      }
      this.saveConfig(settings)
    },
    setConfig() {
      let settings = {
        baseUrl: this.getBaseUrl,
        designW: this.designW,
        designH: this.designH,
        checkboxList: this.checkboxList,
        pageRotation: this.pageRotation, //页面轮播循环时间(秒)
        statisticalKPI: this.statisticalKPI, //统计KPI更新时间(秒)
        devWait: this.devWait, //seamless等待时间
        realTimeKPI: this.realTimeKPI, //实时KPI更新时间(秒)
        componentPageTurning: this.componentPageTurning,//组件翻页时间(秒)
        textScrollingSpeed: this.textScrollingSpeed,//文字滚动速度
        dashboardSpeed: this.dashboardSpeed,//设备运行看板滚动速度
        theme: this.theme,//主题切换
        tableContentSwitching: this.tableContentSwitching,//表格内容切换
      }
      //
      // if (this.checkboxList.length === 0) {
      //   this.$emit('showAside')
      // }
      this.saveConfig(settings)
      this.$emit('setConfig') //会执行父父组件的pageresize,会将scale也保存到localstorage
    },
    cli() {
      this.isCheckAll = !this.isCheckAll
      if (this.isCheckAll) {
        this.checkboxList = this.workshopList.map(item => item.WorkshopName)
      } else {
        this.checkboxList = []
      }
    }
  },
  watch: {
    workshopList: {
      handler(newV, oldV) {
        this.initData()
      },
      deep: true
    }
  },
  mounted() {
    this.getBaseUrl = this.config.baseUrl
  }
}
</script>

<style scoped>
.aside {
  position: absolute;
  z-index: 100;
  color: #fff;
  left: 0;
  bottom: 2rem;
  border: 0.5rem solid transparent;
  background: midnightblue;
  width: 30rem;
  border-radius: 0 1rem 1rem 0;
  box-shadow: 0 0 0.5rem 0.5rem deepskyblue;
  transition: all 0.3s ease;
}
.aside:after {
  content: "";
  display: block;
  position: absolute;
  width: 1rem;
  height: 100%;
  top: 0;
  right: 0;
  transform: translateX(2rem);
}
.aside-hide {
  transform: translateX(-32rem);
}
.input-group ~ .input-group {
  border-top: 0.0625rem solid rgba(135, 206, 235, 0.63);
}
.input-group {
  padding: 1rem 0.5rem;
}
.input-group:last-child {
  text-align: center;
}
h4 {
  margin-bottom: 0.5rem;
}
label {
  margin: 0 0.5rem 0 1rem;
  white-space: nowrap;
}
.input-groupcolors label {
  margin-left: 0;
}
.input-group.other label {
  line-height: 2.5rem;
}
.input-group.other span {
  margin: 0 0.5rem 0 1rem;
}
.input-group.other #all {
  margin-left: 1rem;
}
input {
  padding: 0.125rem 0.25rem;
  width: 6rem;
  font-size: 1.125rem;
  color: #fff;
  border: 0.0625rem solid rgba(255, 255, 255, 0.5);
  background: transparent;
}
input[type="radio"],
input[type="checkbox"] {
  width: auto;
  height: 1rem;
  vertical-align: top;
}
input[type="checkbox"] {
  position: relative;
  top: 0.3rem;
}
#theme-black,
#theme-blue,
#table-row,
#table-page {
  position: relative;
  top: 0.8rem;
  left: 0.3rem;
}
button {
  margin-top: 0.5rem;
  padding: 0.25rem 1rem;
  font-size: 1.25rem;
  color: #fff;
  background: #2196f3;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
}
button:hover {
  background: #4ea3d6;
}
button:active {
  background: #4e93c6;
}
</style>


